<template>
  <div>
    <el-collapse v-model="activeNames">
      <el-collapse-item v-for="(v, ky) in parameters" :name="ky + ''" :key="ky">
        <template slot="title">
          {{ v }}
        </template>
        <el-button type="text" @click="handleBtn('read', ky)">读取 </el-button>
        <el-button type="text" @click="handleBtn('refresh', ky)">刷新 </el-button>
        <div class="form-container">
          <el-row>
            <el-col class="form-item" v-for="(e, i) in paramDatas[ky]" :key="i" :span="8">
              <el-row :gutter="20">
                <el-col :span="8">
                  <el-row type="flex" justify="end"> {{ e.name }} </el-row>
                </el-col>
                <el-col :span="10">
                  <el-input
                    :id="'myInput+' + e.Address + '+' + e.Name"
                    :name="'myInput+' + ky.toString()"
                    disabled
                    :value="e.value"
                    @click.native="handleClick(e)"
                  >
                  </el-input>
                </el-col>
                <el-col :class="{ 'text-reading': !e.value, 'text-success': !!e.value }" :span="6">
                  {{ e.value ? '读取成功' : '读取中' }}
                </el-col>
              </el-row>
            </el-col>
          </el-row>
          <br />
          <el-row type="flex" justify="end">
            <el-button type="primary" @click="commitWrite(ky)"> 写入 </el-button>
          </el-row>
          <!-- 调参修改弹窗 -->
          <el-dialog
            width="50%"
            :title="dialogData.title"
            :show-close="false"
            :visible.sync="dialogVisible"
            :modal-append-to-body="false"
            :close-on-click-modal="false"
          >
            <el-row class="dialog-form">
              <el-row>
                <el-col :span="18">
                  <el-slider
                    :max="dialogData.MaxValue"
                    :min="dialogData.MinValue"
                    v-model="dialogFrom.valueTrue"
                  ></el-slider>
                </el-col>
                <el-col :span="6">
                  <div
                    :style="{
                      width: '80%',
                      marginLeft: '10%'
                    }"
                  >
                    <el-input-number
                      size="mini"
                      v-model="dialogFrom.valueTrue"
                      :max="dialogData.MaxValue"
                      :min="dialogData.MinValue"
                      :step="dialogData.step"
                      :controls-position="'right'"
                    ></el-input-number>
                  </div>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="8">
                  范围： {{ dialogData.MinValue }} ~ {{ dialogData.MaxValue }}
                </el-col>
                <el-col :span="8"> 精度：{{ dialogData.Precision }} </el-col>
                <el-col :span="8"> 单位：{{ dialogData.Unit }} </el-col>
              </el-row>
            </el-row>
            <span slot="footer" class="dialog-footer">
              <el-button @click="dialogVisible = false">取 消</el-button>
              <el-button type="primary" @click="handleDialogCommit">确 定</el-button>
            </span>
          </el-dialog>
        </div>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script>
import _data from './_data'
import _ from 'lodash'
import { time } from 'echarts'

export default {
  name: 'CollapseComponent',
  components: {},
  data() {
    return {
      activeNames: [],
      paramDatas: {},
      dialogVisible: false,
      dialogData: {},
      dialogFrom: {
        valueTrue: 0
      }
    }
  },
  props: ['parameters', 'gensetInfo', 'myProp'],
  //   inject: [''],
  //   provide() {
  //     return {}
  //   },
  computed: {},
  watch: {},
  methods: {
    getNumStr(num) {
      let str = ''
      switch (num) {
        case 0.1:
          str = '保留一位小数'
          break
        case 0.01:
          str = '保留两位小数'
          break
        case 0.001:
          str = '保留三位小数'
          break
        case 0.0001:
          str = '保留四位小数'
          break
        case 0.00001:
          str = '保留五位小数'
          break
        default:
          str = '保留整数'
          break
      }
      return str
    },
    handleDialogCommit() {
      this.dialogVisible = false
      // TODO test code
      const el = document.getElementById('myInput+' + this.dialogData.Address)
      try {
        el.value = this.dialogFrom.valueTrue
      } catch (error) {
        // throw error
      }
    },
    // 50000
    handleBtn(e, ky) {
      const param = this.parameters
      const gensetInfo = this.gensetInfo
      const prop = this.myProp
      const p = {
          GeneratorId: gensetInfo.ID.toString(),
          SheetId: prop.SheetId.toString(),
          Type: Number(ky),
          Name: param[ky],
          IsReadOrder: e === 'read' ? true : false
          // SheetId: '2773',
          // GeneratorId: '8864',
          // IsReadOrder: false,
          // Type: 101,
          // Name: '参数-市电'
        },
        q = {}
      this.$api.operation.getParamsDataV2(p, q).then((value) => {
        // value = _data.read
        // if (e === 'refresh') value = _data.readSuccess
        // if (value.Code != 1) {
        //   this.$message(value.Message || '无数据')
        // }
        const data = value.Data && value.Data.Data
        const paramDatas = new Array()
        if (data && data.length > 0) {
          data.forEach((e) => {
            const obj = {
              name: e.Name,
              value: e.Value,
              ...e
            }
            paramDatas.push(obj)
          })
        } else {
          // 无记录，在
        }
        this.$set(this.paramDatas, ky + '', paramDatas)
        if (value.IsSuccess) this.$message(value.Message)
      })
    },
    handleClick(e) {
      this.dialogVisible = true
      const p = {
        Address: e.Address,
        ProtocolId: e.ProtocolId
      }
      this.$api.operation.GetParamDetailV2(p).then((value) => {
        // value = _.cloneDeep(_data.detail[Number((Math.random() * 5).toFixed(0))])
        const data = value.Data
        // if (value.Code != 1) {
        //   this.$message(value.Message || '无数据')
        // }
        if (data) {
          data.title = `参数修改-${data.ProjectName}`
          data.step = data.Precision
          data.Precision = this.getNumStr(data.Precision)
          this.dialogData = {
            ...data
          }
          this.dialogFrom.valueTrue = Number(e.value)
        }
      })
    },
    // 写入时数据模型与初始化表单模型须一致
    commitWrite(ky) {
      const param = this.parameters
      const gensetInfo = this.gensetInfo
      const prop = this.myProp
      const p = {
        GeneratorId: gensetInfo.ID.toString(),
        SheetId: prop.SheetId.toString(),
        Type: Number(ky),
        Data: []
      }
      const el = document.getElementsByName('myInput+' + ky.toString())
      if (el instanceof NodeList) {
        el.forEach((e) => {
          const address = e.id.split('+')[1]
          const name = e.id.split('+')[2]
          const obj = {
            Address: address,
            Name: name,
            Value: e.value
          }
          p.Data.push(obj)
        })
      }
      console.log(p, ky, param, el[1], this.paramDatas)
      this.$api.operation.addRemoteParamsRecordV2(p).then((value) => {})
    }
  },
  beforeCreate() {},
  created() {
    const activeNames = new Array()
    const paramDatas = new Object()
    for (const ky in this.parameters) {
      activeNames.push(ky + '')
      paramDatas[ky + ''] = []
    }
    this.activeNames = activeNames
    this.paramDatas = paramDatas
  },
  beforeMount() {},
  mounted() {},
  beforeUpdated() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {}
}
</script>
<style lang="less" scoped>
/deep/ .text-reading {
  color: #fdb142;
}
/deep/ .el-input-number--mini {
  width: 100%;
}
/deep/ .el-input.is-disabled .el-input__inner {
  cursor: pointer;
}
.form-item {
  margin: 10px 0;
}
</style>


