<template>
  <div>
    <div class="remote-tuning-operation">
      <el-card class="info-container">
        <div slot="header">
          <span>机组信息</span>
          <el-button
            type="success"
            :style="{
              position: 'absolute',
              right: '110px',
              top: '30px'
            }"
            @click="handleCommitBtn"
          >
            完成服务
          </el-button>
          <el-button
            :style="{
              position: 'absolute',
              right: '30px',
              top: '30px'
            }"
            @click="handleBackBtn"
          >
            返回
          </el-button>
        </div>
        <el-form disabled :inline="true" :model="formInline" class="demo-form-inline">
          <el-form-item label="机组名称">
            {{ formInline.DisplayName }}
          </el-form-item>
          <el-form-item label="云猫标识">
            {{ formInline.CollectorToken }}
          </el-form-item>
          <el-form-item label="控制器品牌">
            {{ formInline.ControllerBrand }}
          </el-form-item>
          <el-form-item label="调参协议">
            {{ formInline.ProtocolName }}
          </el-form-item>
          <el-form-item label="安装地点">
            {{ formInline.InstallationSite }}
          </el-form-item>
          <el-form-item label="通讯协议">
            {{ formInline.GenCoollectorName }}
          </el-form-item>
          <el-form-item label="有人值守">
            {{ formInline.IsOnduty ? '是' : '否' }}
          </el-form-item>
        </el-form>
      </el-card>
      <el-row class="detail-container">
        <el-tabs v-model="activeTabName">
          <el-tab-pane label="参数" name="1">
            <collapse-component
              :myProp="myProp"
              :parameters="renderParameters[0]"
              :gensetInfo="formInline"
            >
            </collapse-component>
          </el-tab-pane>
          <el-tab-pane label="功能" name="2">
            <collapse-component
              :myProp="myProp"
              :parameters="renderParameters[1]"
              :gensetInfo="formInline"
            >
            </collapse-component>
          </el-tab-pane>
          <el-tab-pane label="曲线" name="3">
            <collapse-component
              :myProp="myProp"
              :parameters="renderParameters[2]"
              :gensetInfo="formInline"
            >
            </collapse-component>
          </el-tab-pane>
          <el-tab-pane label="测量" name="4">
            <collapse-component
              :myProp="myProp"
              :parameters="renderParameters[3]"
              :gensetInfo="formInline"
            >
            </collapse-component>
          </el-tab-pane>
        </el-tabs>
      </el-row>
    </div>
  </div>
</template>

<script>
import _data from './_data'
import CollapseComponent from './CollapseComponent.vue'

export default {
  name: 'RemoteTuningOperation',
  components: {
    CollapseComponent
  },
  data() {
    return {
      // form
      formInline: {
        DisplayName: '',
        CollectorToken: '',
        ControllerBrand: '',
        ProtocolName: '',
        InstallationSite: '',
        GenCoollectorName: '',
        IsOnduty: ''
      },
      // tab option
      activeTabName: '1',
      // collapse group
      collapseOpt: {
        1: {
          101: '参数-市电',
          102: '参数-发电',
          103: '参数-发动机',
          104: '参数-系统/延时',
          105: '参数-同步/负载',
          106: '参数-通信',
          107: '参数-监测',
          108: '参数-维护保养',
          109: '参数-模拟量校准',
          110: '参数-开关量输入',
          111: '参数-模拟量输入',
          112: '参数-开关量/PWM输出',
          113: '参数-保留',
          114: '参数-可编程输出',
          115: '参数-可编程输入',
          116: '参数-校验'
        },
        2: {
          201: '功能-市电',
          202: '功能-发电',
          203: '功能-发动机',
          204: '功能-系统/延时',
          205: '功能-同步/负载',
          206: '功能-通信',
          207: '功能-监测',
          208: '功能-维护保养',
          209: '功能-模拟量校准',
          210: '功能-开关量输入',
          211: '功能-模拟量输入',
          212: '功能-开关量/PWM输出',
          213: '功能-保留'
        },
        3: {
          301: '图表-温度传感器',
          302: '图表-压力传感器',
          303: '图表-液位传感器',
          304: '图表-机油压力监测',
          305: '图表-工况统计',
          306: '图表-测量转速',
          307: '图表-限功率',
          308: '图表-电子铭牌',
          309: '图表-保留'
        },
        4: {
          401: '测量-市电',
          402: '测量-发电',
          403: '测量-发动机',
          404: '测量-系统状态',
          405: '测量-同步/负载',
          406: '测量-通信',
          407: '测量-监测',
          408: '测量-维护保养',
          409: '测量-输入状态',
          410: '测量-开关量输入',
          411: '测量-模拟量输入',
          412: '测量-谐波分析',
          413: '测量-保留',
          414: '测量-历史记录'
        }
      },
      collapseOptNamesMapping: {
        1: '参数',
        2: '功能',
        3: '图表',
        4: '测量'
      },
      writeRecords: []
    }
  },
  props: ['myProp'],
  // inject: [''],
  // provide() {
  //   return {}
  // },
  computed: {
    renderParameters() {
      const newParameters = []
      const p = this.writeRecords
      for (const ky in this.collapseOpt) {
        const obj = {}
        const temp = this.collapseOpt[ky]
        for (const ky2 in temp) {
          if (p.includes(ky2)) {
            obj[ky2] = temp[ky2]
          }
        }
        newParameters.push(obj)
      }
      return newParameters
    }
  },
  watch: {
    myProp: {
      handler: function (nv) {
        this.getData()
      },
      immediate: true
    }
  },
  methods: {
    handleBackBtn() {
      this.$emit('changeIs', 'MainPage')
    },
    handleCommitBtn() {
      this.$api.operation.completeRemoteParams().then((value) => {
        this.$emit('changeIs', 'MainPage')
      })
    },
    getData() {
      const prop = this.myProp
      const p = {
          generatorId: prop.GeneratorSetId,
          id: prop.No
        },
        q = {}
      this.$api.operation.getProtocolParmasByGeneatorId(p, q).then((value) => {
        // value = _data.initData
        const gensetInfo = value.Data && value.Data.genset
        const list = value.Data && value.Data.list
        const writeRecords = value.Data && value.Data.writeRecords
        if (gensetInfo) {
          this.formInline = {
            ...gensetInfo
          }
        }
        if (list && Array.isArray(list)) {
        }
        if (writeRecords && Array.isArray(writeRecords)) {
          this.writeRecords = writeRecords.map((e) => e.toString())
        }
      })
    }
  },
  beforeCreate() {},
  created() {},
  beforeMount() {},
  mounted() {},
  beforeUpdated() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {}
}
</script>
<style lang='less'  scoped>
.remote-tuning-operation {
  display: flex;
  flex-direction: column;
}
</style>


